.add-fields-list {
	list-style: none;
	width: 100%;
	box-sizing: border-box;
	margin: 0;
	padding: 0px;
	/* border: 1px solid #e0e0e0; */
}
.add-fields-list i {
	margin-right: 10px;
}
.add-fields-list li {
	padding: 10px 0px 10px 17px;
	font-size: 14px;
	color: #4b5966;
	border: 1px solid #fff;
	margin-bottom: 0px;
	cursor: pointer;
	border-bottom: 1px solid #e0e0e0;
	width: auto;
}
.add-fields-list li.profield-cont {
	cursor: auto;
}
.add-fields-list li:last-child {
	border-bottom: 0px solid #e0e0e0;
}
.add-fields-list li:hover {
	color: #4199fd;
	background: #f6f7fb;
}

.add-fields-list li .description {
	color: #999;
	font-size: 12px;
	font-style: italic;
}
.add-fields-list li:first-child {
	margin-top: 10px;
}
.add-fields-list li:last-child {
	margin-bottom: 10px;
}
.fields_tabs_cont {
	display: flex;
}
.fields_tabs_cont .components-tab-panel__tabs {
	border-right: 1px solid #e0e0e0;
	width: 30%;
}
.fields_tabs_cont .components-tab-panel__tab-content {
	width: 70%;
}

.view_field_modal .components-modal__header {
	border-bottom: 1px solid #e0e0e0;
	padding: 0px 16px;
}
.view_field_modal .components-modal__content {
	padding: 0px;
	/* overflow-y: hidden; */
}
.components-modal__frame.view_field_modal {
	width: 500px;
}
.form_fields_tab.active-tab,
.entry_meta_tab.active-tab {
	box-shadow: inset 0 0 0 var(--wp-admin-border-width-focus) transparent,
		inset 0 -1.5px 0 0 var(--wp-admin-theme-color) !important;
	position: relative;
}
