.filter_field_dropdown {
	width: 200px;
	margin-right: 0px;
}
.filter_condition_dropdown {
	width: 120px;
	margin-left: 5px;
}

.filterValue {
	width: 190px;
}
.resetValueTypeCont {
	text-align: right;
	margin-right: 6px;
	font-size: 12px;
}
.resetValueType {
	border-width: 0px;
	background: transparent;
	text-decoration: underline;
}
#mergeTags {
	border: 1px solid #e0e0e0;
	color: #000;
	pointer-events: all;
	padding: 0px;
	width: 400px;
}

.mergeTagsIcon {
	margin-left: -26px !important;
	font-size: 12px !important;
	margin-right: 10px;
	background-color: #ffffff;
}

.filter-upgrade-msg {
	width: 70%;
	margin: 0 auto;
	position: relative;
	box-shadow: 0 0 60px 30px rgba(0, 0, 0, 0.15);
	border-radius: 6px;
}
.filter-upgrade-msg p {
	font-size: 16px;
}
.filter-upgrade-msg .upgrade-text {
	padding: 20px;
}
.filter-upgrade-msg .ftr {
	border-radius: 0 0 3px 3px;
	padding: 30px;
	background: #f5f5f5;
	text-align: center;
}
.filter-upgrade-msg a {
	background-color: #005cb8;
	color: #fff;
	padding: 15px;
	text-decoration: none;
}
.components-button.is-primary.add-new-and-cond {
	margin-left: 0px;
	margin-right: 10px;
	padding: 8px 12px;
	height: auto;
}
.flyout_fields_cont.filter-group-rows {
	margin: 0px 10px;
	border: 1px solid #e0e0e0;
}
.filter-group-rows .condition_field_row {
	padding: 8px 0px;
}
.filter-group-rows .condition_field_row:last-child {
	padding-bottom: 0px;
}
.filter-group-rows .condition_field_row .components-base-control__field {
	margin-bottom: 0px;
}
.or-group-sep {
	margin: 0 auto;
	width: 38px;
}
.or-group-sep span {
	font-size: 13px;
	text-transform: uppercase;
	padding: 0.33em 0.6em;
	background: #f5f5f5;
	border: 1px solid #e0e0e0;
	color: #606a73;
	font-weight: 500;
	border-radius: 2px;
	text-align: center;
}
.or-group-sep:before,
.or-group-sep:after {
	content: "";
	width: 0.1em;
	height: 0.8em;
	background: #b5bcc2;
	display: block;
	margin: 0 auto 0.25em;
}
div.or-group-sep:last-child {
	display: none;
}
.filter-new-group-btn {
	margin-left: 10px;
	margin-top: 20px;
}
