.view-section-container {
	padding: 5px 15px 10px;
	/* box-shadow: 0px 0px 7px 1px #EBEBEB; */
	margin-bottom: 25px;
	/* border-radius: 4px; */
	/* border:1px solid #e5e5e5 */
	/* border: 1px solid #e0e0e0; */
	background: #fff;
}

#views-container h2.viewSecHeading {
	font-size: 16px;
	/* color: #4b5966; */
	text-transform: capitalize;
	font-weight: 500;
	letter-spacing: 0.3px;
}
.section-heading-cont {
	display: flex;
	justify-content: space-between;
}
.view-builder-area {
	background: #fff;
}
.view-builder-area > legend {
	margin-left: 15px;
}
.viewTabChild {
	margin-left: 10px;
	margin-right: 10px;
	margin-top: 34px;
	/* border: 1px solid #e0e0e0; */
}
.viewTabChild:last-child {
	margin-bottom: 10px;
}
.viewTabHeadings li {
	line-height: 1.6;
}

.viewTabHeadings li:first-child,
.viewTabHeadings li:first-child a {
	border-top-left-radius: 4px;
}
.viewTabHeadings li a {
	color: #4b5966;
}
.pure-menu-active > .pure-menu-link,
.pure-menu-link:focus,
.pure-menu-link:hover {
	background-color: #f5f5f5;
}

.viewSubHead {
	font-size: 16px;
	color: #6f767b;
	font-weight: 600;
}

.viewInlineSec label {
	margin-right: 10px;
	font-size: 16px;
	width: 175px;
	display: inline-block;
}

.viewInlineSec {
	margin-bottom: 10px;
}
.viewHide {
	display: none !important;
}
.activeRow {
	background-color: #dcf7e0;
	transition: all 0.4s ease-in-out;
}
.single-entry-upgrade-msg {
	text-align: center;
	width: 730px;
	box-shadow: 0 0 60px 30px rgba(0, 0, 0, 0.15);
	border-radius: 6px;
	position: absolute;
	top: 95px;
	left: 50%;
	margin: 0 auto 0 -369px;
	z-index: 100;
	overflow: hidden;
	font-size: 14px;
}
.single-entry-upgrade-msg p {
	font-size: 16px;
	padding: 0px 20px;
}
.single-entry-upgrade-msg .ftr {
	border-radius: 0 0 3px 3px;
	padding: 30px;
	background: #f5f5f5;
	text-align: center;
}
.single-entry-upgrade-msg a {
	background-color: #005cb8;
	color: #fff;
	padding: 15px;
	text-decoration: none;
}
