.views_toolbar_cont {
	padding: 12px 0.9375rem;
	display: flex;
	justify-content: space-between;
	max-width: 100%;
	width: calc(100% - 180px);
	align-items: center;
}
.views_toolbar_settings {
	display: flex;
	max-width: 800px;
	align-items: baseline;
}
.view_title {
	width: 400px;
}
.view_title .components-text-control__input {
	border-width: 0px;
	font-size: 16px;
}

.toolbar_setting_cont {
	width: 200px;
	margin-right: 60px;
}
.title-placeholder {
	margin-right: 0px;
	font-size: 16px;
}

.top_setting_label {
	font-size: 11px;
	text-transform: uppercase;
	margin-bottom: 10px;
}
.toolbar_setting_cont .components-popover {
	text-align: left;
}
.toolbar_setting_cont .components-popover__content .form_list_row {
	padding: 10px;
	cursor: pointer;
}
.toolbar_setting_cont .components-popover__content .form_list_row:hover {
	color: #4199fd;
	background: #f6f7fb;
}
.views_toolbar_buttons {
	display: flex;
	align-items: end;
	width: 350px;
	justify-content: flex-end;
}

.view_embed_shortcode_cont {
	margin-right: 20px;
	color: #4b5966;
	margin-top: 0px;
}
.save_view_btn {
	width: 125px;
}
.view_embed_shortcode_popup {
	padding: 20px;
}
.view_embed_shortcode_content {
	background: #fff;
	border-radius: 3px;
	/* box-shadow: 0 1px 2px rgb(0 0 0 / 5%); */
	display: flex;
	margin-right: 10px;
	width: 300px;
	align-items: center;
	gap: 5px;
}
.view_embed_shortcode_content svg {
	width: 20px;
	margin-right: 5px;
}

.view_embed_shortcode {
	padding: 10px;
	background: #f1f1f1;
}
.views_toolbar_buttons button {
	font-size: 14px;
	padding: 6px 20px !important;
	border-radius: 3px;
}
.views_toolbar_buttons button i {
	margin-right: 6px;
}
.view_copy_shortcode {
	cursor: pointer;
}
