.row {
	/* border: 1px solid red; */
	position: relative;
	/* padding: 10px; */
	margin: 15px 0px;
	background: #fff;
	/* box-shadow: 0px 1px 8px 0px rgba(0, 0, 0, 0.15);
	border-radius:4px;
	border: 1px solid #e2e4e7;*/
}

.view-setting-icon {
	display: inline-block;
	width: 25px;
}
.view-row-remove:hover {
	color: red;
}
.rowSettingsPanel {
	background: #f5f5f5;
	padding: 5px 10px;
	margin: 0px 0 0px -1px;
	font-size: 16px;
	color: #4b5966;
	border-top-left-radius: 0px;
}

.columnPanel {
	border: 1px solid #e0e0e0;
	width: 100%;
	box-sizing: border-box;
	padding: 10px;
}
.addRow {
	padding: 5px;
	cursor: pointer;
	box-sizing: border-box;
	font-size: 14px;
	text-align: center;
	display: block;
	border: 1px dashed #e0e0e0;
	padding: 10px;
	/* margin: 0px 10px; */
}

.addRow span {
	color: #8294a5;
}
.row-layout-icon {
	cursor: pointer;
}
.hiddendrag {
	display: none;
}
