#views-container {
	margin: auto;
	position: fixed;
	width: 100%;
	height: 100vh;
	background: #fff;
}

.views-wrapper {
	flex-grow: 1;
	display: flex;
	overflow: auto;
	overscroll-behavior-y: none;
	position: relative;
	border-top: 1px solid #e0e0e0;
	border-bottom: 1px solid #e0e0e0;
	max-width: 100%;
	width: calc(100% - 160px);
}

.view-builder-area {
	height: calc(100vh - 64px);
	/* margin-bottom: 25px; */
	overflow: auto;
	position: relative;
	width: 100%;
}
.view-sidebar {
	min-width: 300px;
	max-width: 300px;
	background: #fff;
	height: calc(100vh - 64px);
	overflow: auto;
}
.view-builder-cont {
	overflow: auto;
	height: 100%;
	border-right: 1px solid #e0e0e0;
}

.viewTabHeadings {
	text-align: center;
	/* background: #e9ecf5; */
	background: #f5f5f5;
	width: 100%;
	margin: 0 auto;
	padding: 4px 0 5px;
	/* margin-top: 10px; */
	position: absolute;
	margin-left: auto;
	margin-right: auto;
	left: 0;
	right: 0;
	z-index: 99;
	/* background: #fff; */
	border-bottom: 1px solid #e0e0e0;
}
.viewTabHeadings li {
	cursor: pointer;
	margin: 0 0.2em 1px 0;
	list-style: none;
	position: relative;
	top: 2px;
	margin: 0 0.2em 1px 0;
	/* border-bottom: 0; */
	padding: 0;
	white-space: nowrap;
	outline: none;
}
.viewTabHeadings li a {
	/* border: 1px solid #ccc; */
	border-bottom: none;
	margin-left: 0.5em;
	padding: 5px 10px;
	font-size: 16px;
	line-height: 1.71428571;
	/* background: #e0e0e0; */
	color: #555;
}
.viewTabHeadings li a:hover {
	background-color: #fff;
	border-bottom: 1px solid #fff;
	border-radius: 4px;
}
.viewTabHeadings li a i {
	margin-right: 5px;
}
.viewTabHeadings li.viewTabActive a {
	background-color: #fff;
	border-bottom: 1px solid #fff;
	border-radius: 4px;
	box-shadow: 0 0 0 1px #4f94d4, 0 0 2px 1px rgb(79 148 212 / 80%);
}

.view-section-container:first-child {
	margin-top: 50px;
}

#views-container .components-panel__body-toggle.components-button {
	padding: 20px 48px 20px 16px;
}

.view_builder_sections {
	padding-bottom: 100px;
}
.view-sidebar .components-tab-panel__tabs {
	border-left: 1px solid #e0e0e0;
}
.components-tab-panel__tabs-item.is_active_side_tab {
	box-shadow: inset 0 0 0 var(--wp-admin-border-width-focus) transparent,
		inset 0 -1.5px 0 0 var(--wp-admin-theme-color) !important;
	position: relative;
}
.field_info_header {
	display: flex;
	padding: 16px 16px;
	justify-content: flex-start;
}
.sidebar_field_icon {
	display: flex;
	font-size: 1.5rem;
	justify-content: flex-start;
	width: 35px;
}
.sidebar_field_description {
	width: 85%;
}
.sidebar_field_label {
	color: #242748;
	font-size: 13px;
	font-weight: 500;
	height: 18px;
	line-height: 1.188rem;
	text-align: left;
}
.sidebar_field_info {
	display: flex;
	justify-content: space-between;
}
.sidebar_field_text {
	color: #242748;
	font-size: 0.8125rem;
	height: auto;
	line-height: 1.188rem;
	padding: 10px 0px 0px 0px;
	text-align: left;
}
.view-child {
	margin-left: 10px;
}

.views_flyout_cont {
	overflow: hidden;
	background: #fff;
	bottom: 0;
	box-shadow: 0 0 1px rgb(18 25 97 / 24%), 0 24px 24px rgb(18 25 97 / 3%),
		0 2px 2px rgb(18 25 97 / 3%), 0 4px 4px rgb(18 25 97 / 3%),
		0 8px 8px rgb(18 25 97 / 3%), 0 16px 16px rgb(18 25 97 / 3%);
	box-sizing: border-box;
	flex-direction: column;
	height: 100%;
	padding-top: 1.25rem;
	padding-bottom: 1.25rem;
	position: absolute;
	top: 0;
	width: 700px;
	max-width: 700px;
	opacity: 1;
	transition: opacity 190ms ease-in-out, transform 190ms ease-in-out;
	z-index: 100;
	display: flex;
	left: auto;
	right: 300px;
	overflow-y: scroll;
}
.views_flyout_cont.is_open {
	opacity: 1;
	transition: opacity 190ms ease-in-out, transform 190ms ease-in-out;
	z-index: 100;
	display: flex;
	left: auto;
	right: 300px;
	overflow-y: scroll;
}
.views_flyout_cont i {
	cursor: pointer;
}

#tab-panel-0-fieldSettingTab-view .components-panel {
	border: none;
	border-top: 1px solid #e0e0e0;
}

.pro-features {
	background: #ccc;
	padding: 2px 2px;
	border-radius: 4px;
}

#views-container .components-button.is-secondary {
	border: 1px solid #d5d7e9;
	box-shadow: 0 1px 2px rgb(0 0 0 / 5%);
	border-radius: 3px;
	color: #4b5966;
}
#views-container .components-panel__row label {
	max-width: 95%;
}
#views-container .components-panel__row .components-base-control {
	width: 100%;
}
.pro-upsell-link {
	color: #cd631d;
	font-size: 13px;
	margin-left: 10px;
	font-weight: 700;
}
.pro-upsell-link i {
	margin-left: 5px;
	font-size: 12px;
}
.upgrade-tip {
	font-size: 12px;
}

[data-react-beautiful-dnd-drag-handle="0"] {
	cursor: move !important;
	pointer-events: auto !important;
}
