.views-calculations-settings {
	padding: 10px;
	font-size: 16px;
}
.calcRow {
	padding: 10px 0px;
}
.calcRow span {
	display: block;
	margin-bottom: 10px;
}
.calcRow .calcName {
	width: 200px;
}
.calcRow .calcDecimals {
	width: 30px;
	margin-left: 5px;
}
.calcRow i {
	font-size: 22px;
	color: #8294a5;
	vertical-align: middle;
	margin-left: 5px;
	cursor: pointer;
}
#calcMergeTags {
	border: 1px solid #e0e0e0;
	color: #000;
	pointer-events: all;
	padding: 0;
	width: 250px;
}
#calcMergeTags ul {
	list-style: none;
	/* margin-left: 0px; */
	padding-left: 20px;
	max-height: 400px;
	overflow-y: auto;
}
.mergeTagsToolTip ul li {
	padding: 5px 0px;
	margin-bottom: 5px;
	/* background: #fff; */
}
