.field_row {
	margin-bottom: 12px;
	/* background: #f5f5f5; */
	/* color: #4b5966; */
	font-size: 14px;
	letter-spacing: 0.6px;
	overflow: hidden;
	text-overflow: ellipsis;
	box-sizing: border-box;
	border-radius: 2px;
	border: 1px solid #e0e0e0;
	/* padding: 0px 5px; */
	margin-left: 5px;
	margin-right: 5px;
}
.field_row:hover {
	background-color: #f3f5f7;
}

.field_row.activeField {
	background-color: rgba(66, 66, 66, 0.5);
}
.field_label_row {
	width: calc(100% - 116px);
	display: inline-block;
	cursor: pointer;
	padding: 10px 0px;
}
.field_drag_icon {
	border-right: 1px solid #e0e0e0;
}

.field_row span {
	margin: 0px 5px;
}
.field_row .hidden-label {
	display: inline-block;
}

.field_row span.add-new-field-icon,
.field_row span.close {
	cursor: pointer;
}
.field_row span.add-new-field-icon:hover {
	color: #4f94d4;
}

.field_row span.close:hover {
	color: red;
}

.field_row i {
	margin-right: 10px;
}
