.merge_tag_tabs_cont {
	display: flex;
	width: 340px;
	height: 400px;
}
.merge_tag_tabs_cont .components-tab-panel__tabs {
	border-right: 1px solid #e0e0e0;
}
.views_flyout .components-button.merge_list_tab {
	margin-left: 0px;
}
.merge_tag_tabs_cont ul {
	padding: 10px 5px;
	margin: 0px;
	text-decoration: none;
	list-style: none;
	font-size: 14px;
}
.merge_tag_tabs_cont ul li {
	padding: 10px 0px;
}
.merge_tag_tabs_cont a {
	color: #4b5966;
	text-decoration: none;
}
.merge_tag_tabs_cont a:hover {
	color: var(--wp-admin-theme-color);
}
.merge_list_tab.active-tab {
	box-shadow: inset 0 0 0 var(--wp-admin-border-width-focus) transparent,
		inset 0 -1.5px 0 0 var(--wp-admin-theme-color) !important;
	position: relative;
}
