.components-modal__frame.view_layout_modal {
	max-width: 800px;
}
.components-modal__frame.view_field_modal {
	max-width: 800px;
}

.layoutCol:first-child .layoutColStyle {
	margin-left: 0px;
}

.layoutCol:last-child .layoutColStyle {
	margin-right: 0px;
}

.container {
	list-style-type: none;
	display: inline-block;
	width: 100%;
	box-sizing: border-box;
	margin: 0px;
	padding: 10px;
}

.layoutHead {
	padding: 10px 21px;
	/* background-color: #f3f4f5; */
	background-color: #f0f8ff;
	color: #4b5966;
	position: sticky;
	top: -20px;
	z-index: 9;
	border-bottom: 1px solid #e2e4e7;
}

h2.layoutHeading {
	font-size: 16px !important;
	margin: 0;
	font-weight: 400;
	padding: 9px 0 4px 0 !important;
}

.container li {
	float: left;
	width: 32.3%;
	margin: 0px 1.5% 20px 0;
	padding: 10px 10px;
	cursor: pointer;
	box-sizing: border-box;
	/* border-radius: 4px; */
	box-shadow: 0px 1px 1px 0px rgba(0, 0, 0, 0.05);
}

.container li:nth-child(3n) {
	margin-right: 0px;
}

.closeLayoutOption {
	position: absolute;
	top: 0px;
	right: 0px;
	bottom: 0px;
	padding: 10px 21px;
	background-color: #f0f8ff;
	font-size: 24px;
	cursor: pointer;
	margin-top: 0px !important;
	margin-right: 0px !important;
}

.layoutCol {
	/* float: left; */
	position: relative;
}

.layoutColStyle {
	margin: 0 5px;
	height: 40px;
	background-color: #f5f5f5;
	color: #4b5966;
	text-align: center;
	border: 1px dotted #e0e0e0;
}

.layoutName {
	padding-top: 12px;
	display: inline-block;
	font-size: 16px;
}

/* last row element has no margin */

.container li.view-layout-last-row {
	margin-bottom: 0px;
}
