.search_fields_cont_header {
	display: flex;
	justify-content: flex-start;
	padding: 20px 25px;
	align-items: baseline;
	border-bottom: 1px solid #e0e0e0;
}
.search_fields_cont_header div {
	width: 200px;
	margin-left: 20px;
	font-size: 14px;
}
.search_fields_cont_header div:last-child {
	width: 100px;
}
.row_drag_handle {
	width: 20px;
}
.search_field_dropdown,
.search_field_label {
	width: 200px;
	margin-right: 20px;
}
.search_field_dropdown select,
.search_field_label input {
	width: 200px !important;
}
.search_field_type select {
	width: 100px;
}
