.col-cont {
	height: 100%;
}

.column .title {
	width: 100%;
	padding: 0 10px;
	text-transform: capitalize;
	display: inline-block;
}

.column .activeColumn {
	background-color: #dcf7e0;
	transition: all 0.4s ease-in-out;
}

.view-empty-placeholder {
	box-sizing: border-box;
	font-size: 14px;
	color: #4f94d4;
	cursor: pointer;
	height: 100%;
	border: 1px dashed #e2e4e7;
	min-height: 28px;
	padding: 7px 0;
	text-align: center;
	/* margin-top: 0px; */
	margin-bottom: 12px;
	margin-left: 5px;
	margin-right: 5px;
}
.view-empty-placeholder:hover {
	border: 1px dashed #e2e4e7;
}
.view-empty-placeholder i {
	margin: 6px 5px;
}

.droppableColumn {
	min-height: 41px;
}
