.flyout_heading {
	padding: 10px 25px;
	border-bottom: 1px solid #e0e0e0;
	display: flex;
	justify-content: space-between;
	font-size: 16px;
	margin-bottom: 10px;
}
.views_flyout .components-button {
	margin-left: 25px;
}
.flyout_fields_cont {
	padding: 15px 25px 20px 25px;
	background: #f5f5f5;
	margin: 10px 10px;
}
.flyout_fields_cont i {
	cursor: pointer;
}

.condition_field_row {
	border-bottom: 0px solid #e0e0e0;
	display: flex;
	justify-content: flex-start;
	padding: 12px 0px;
	align-items: baseline;
}
.condition_field_row .components-base-control {
	display: inline-block;
	margin-right: 10px;
}
.condition_field_row i {
	margin-right: 15px;
}
